<style lang="less" scoped>
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.box {
  width: 375px;
  height: 768px;
  position: relative;
  .content_img {
    width: 100%;
    height: 100%;
    background-image: url(../images/bg.png);
