      height: 100%;
      float: left;
      background-color: #fff;
      border: 1px solid #fff;
      font-size: 18px;
      padding-left: 30px;
      position: relative;
      border-left: 1px solid #eee;
      border-bottom-left-radius: 10px;
      border-bottom-right-radius: 10px;

      > img {
        width: 16px;
        height: 16px;
        position: absolute;
        left: 25px;
        bottom: 15px;
      }
    }
    .text_color {
      color: #0085f9;
    }
  }
  .shareBill {
    width: 300px;
    height: 500px;
    border-radius: 15px;
