axios.interceptors.response.use(response => {
  return response
}, err => {
  if (err && err.response) {
    switch (err.response.status) {
      case 400: console.log('')
        break; case 401: console.log('')
        break; case 403: console.log('')
        break; case 404: console.log(',')
        break; case 405: console.log('')
        break; case 408: console.log('')
        break; case 500: console.log('')
        break; case 501: console.log('')
        break; case 502: console.log('')
        break; case 503: console.log('')
        break; case 504: console.log('')
        break; case 505: console.log('http')
        break; default: console.log(`${err.response.status}`)
    }
  } else {
    console.log('')
  }
  return Promise.resolve(err.response)
})

