<template>
  <div>
    <div id="clear_flex">
      <!--  -->
      <div class="top_title"></div>
      <!--  -->
      <div class="content">
        <!--  -->
        <div class="content_img"></div>
        <!--  -->
        <div class="content_title">
          <img src="../images/content_title.png" alt />
        </div>
        <!--  -->
        <div class="content_from content_from_two">
          <img src="../images/content_from_two.png" alt />
          <van-form class="content_from_id">
            <van-field
              class="id_text"
              v-model="phone"
              type="phone"
              label=""
              placeholder=""
              :rules="[{ required: true}]"
            />
            <div class="content_from_info">
              <van-row class="content_from_info_row">
                <van-col span="8" class="col"></van-col>
                <van-col span="16" class="content_from_info_col">{{idList.name}}</van-col>
              </van-row>
              <van-row class="content_from_info_row">
                <van-col span="8" class="col"></van-col>
                <van-col
                  span="16"
                  class="content_from_info_col"
                >{{idList.province}}{{idList.city}}{{idList.country}}{{idList.town}}{{idList.burg}}</van-col>
              </van-row>
              <van-row class="content_from_info_row">
                <van-col span="8" class="col_zhan"></van-col>
                <van-col span="16" class="content_from_info_col">{{idList.type}}</van-col>
              </van-row>
            </div>

            <van-button
              round
              block
              type="info"
              @click="showOverlay"
              class="idBtn content_from_two_btn"
            ></van-button>
          </van-form>
        </div>
      </div>
    </div>
    <div class="box">
      <!--  -->

      <!--  -->
      <van-overlay :show="isshow">
        <!--  -->
        <div class="top_title"></div>
        <div class="closeCPM" @click="closeCPM">
          <img src="../images/closeCPM.png" alt />
        </div>
        <div class="wrapper" @click.stop>
          <div class="block">
            <p></p>
            <div class="content_from_info">
              <van-row class="content_from_info_row" id="row_name">
                <van-col span="8" class="col"></van-col>
                <van-col span="16" class="content_from_info_col">{{idList.name}}</van-col>
              </van-row>
              <van-row class="content_from_info_row">
                <van-col span="8" class="col"></van-col>
                <van-col
                  span="16"
                  class="content_from_info_col"
                >{{idList.province}}{{idList.city}}{{idList.country}}{{idList.town}}{{idList.burg}}</van-col>
              </van-row>
              <van-row class="content_from_info_row" id="row_type">
                <van-col span="8"></van-col>
                <van-col span="16" class="content_from_info_col">{{idList.type}}</van-col>
              </van-row>
            </div>
            <div class="btn">
              <van-button :class="[isClick == true? 'text_btn': '']" @click="btn(1)"></van-button>
              <van-button :class="[isClick == false? 'text_btn': '']" @click="btn(2)"></van-button>
            </div>
          </div>
        </div>
      </van-overlay>
    </div>
  </div>
</template>

<script>
import QRCode from "qrcodejs2";
export default {
  name: "messengerYuYue",
  components: {
    QRCode
  },
  data() {
    return {
      // 
      phone: "",
      idList: {
        name: "",
        province: "",
        city: "",
        country: "",
        town: "",
        burg: "",
        type: ""
      },
      isActive: 0,
      btnColor: false,
      isShow: true,
      show: false,
      isshow: false,
      isClick: false,
      qrcodeContainer: ''

    };
  },
  created() {
    this.getphone();
  },
  methods: {
    getphone() {
      var phone = this.$route.query.phone;
      //  console.log(card);
      this.phone = phone;
      this.$api
        .searchID({
          phone: this.phone,
          token:
            "eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9.eyJpYXQiOjE1OTA2NDQyOTQsImp0aSI6ImJhZDA2MzdiMDNmZmIxZmViMjg1ZjFlMjhjMjg1ZGE0IiwiaXNzIjoiMTI3LjAuMC4xIiwibmJmIjoxNTkwNjQ0Mjk0LCJleHAiOjE1OTE1MDgyOTQsImRhdGEiOnsidXNlcl9pZCI6MTY2fX0.JYshJF-E3mhbAXJbV13vup87Flt4FpSAyy6jrncAfXA"
        })
        .then(res => {
          console.log(res);
          console.log('');
          
          this.isShow = false;
          this.idList = res.data;
          // console.log(this.IDlist);
        })
    },
    //  
    form_val() {
      this.show = true;
      //  this.$router.push("/messenginfo");
    },
    // 
    showOverlay() {
      this.isshow = true;
    },
    closeCPM() {
      this.isshow = false;
    },

    btn(e) {
      if (e == 1) {
        this.isClick = true;
        this.$router.push("/yuyue");
      } else {
        this.isClick = false;
        this.$router.push("/qrcode");
        
      }
    }
  },
  watch: {
    IDcard: function(newVal) {
      if (newVal.length > 0) {
        this.btnColor = true;
      } else {
        this.btnColor = false;
      }
    }
  }
};
</script>

<style lang="less" scoped>
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
#clear_flex:after {
  content: "";
  clear: left;
  display: block;
}
.top_title {
  width: 375px;
  height: 44px;
  font-size: 18px;
  line-height: 44px;
  text-align: center;
  font-family: ;
  font-weight: 500;
  color: #333;
  background-color: #ffffff;
}
.content {
  width: 375px;
  height: 768px;
  position: relative;
  .content_img {
    position: relative;
    width: 100%;
    height: 100%;
    background-image: url(../images/bg.png);
    background-attachment: fixed;
    background-repeat: no-repeat;
    background-size: cover;
    float: left;
  }
  .content_title {
    position: absolute;
    top: 49px;
    left: 53px;
    width: 261px;
    height: 90px;
    > img {
      width: 100%;
      height: 100%;
    }
  }
  .content_from {
    position: absolute;
    top: 154px;
    left: 19px;
    width: 337px;
    height: 286px;
    > img {
      width: 100%;
      height: 100%;
    }
    .content_from_id {
      float: right;
      position: absolute;
      top: 120px;
      width: 315px;
      height: 48px;
      .id_text {
        font-size: 17px;
        margin-left: 12px;
        color: #333;
      }
      .idBtn {
        width: 238px;
        height: 38px;
        margin-top: 42px;
        margin-left: 49px;
        background-color: #d8e9ff;
        border: 1px solid #d8e9ff;
      }
      .btnColor {
        background-color: #006bf6;
        border: 1px solid #006bf6;
      }
    }
  }
  .content_from_two {
    width: 327px;
    height: 418px;
    margin-left: 5px;
    > img {
      width: 100%;
      height: 100%;
    }
    .content_from_id {
      width: 304px;

      margin: 0 10px;
      .content_from_info {
        width: 304px;

        .content_from_info_row {
          font-size: 17px;
          color: #999;
        
          margin-top: 20px;
          .content_from_info_col {
            color: #333;
            // width: 170px;
            text-align: left;
          }
          .col {
            padding-left: 43px;
          }
          .col_zhan {
            padding-left: 9px;
          }
        }
      }
      .content_from_two_btn {
        margin-top: 45px;
        margin-left: 35px;
        background-color: #006bf6;
      }
      .van-cell:not(:last-child)::after {
        border-bottom: 0;
      }
    }
  }
  .content .content_from_two .content_from_id .content_from_info .content_from_info_row {
    height: 0!important;
  }
}
// 

.box {
  position: relative;
  .top_title {
    width: 375px;
    height: 44px;
    font-size: 18px;
    line-height: 44px;
    text-align: center;
    font-family: ;
    font-weight: 500;
    color: #333;
    background-color: #ffffff;
  }
  .wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
  }
  .closeCPM {
    position: absolute;
    top: 105px;
    right: 24px;
    width: 40px;
    height: 40px;
    > img {
      width: 100%;
      height: 100%;
    }
  }
  .content
    .content_from_two
    .content_from_id
    .content_from_info
    .content_from_info_row
    .content_from_info_col {
    color: #333;
  }
  .block {
    width: 327px;
    height: 305px;
    background-color: #fff;
    border-radius: 10px;
    position: relative;
    > p {
      font-size: 18px;
      font-weight: 600;
      text-align: center;
      margin-top: 20px;
    }
    .content_from_info {
      width: 304px;
      height: 132px;
      margin-left: 10px;
      .content_from_info_row {
        font-size: 17px;
        color: #999;
        height: 40px;
        line-height: 20px;
        // margin-top: 20px;
        .content_from_info_col {
          color: #333;
          font-size: 17px;
          // width: 201px;
          // height: 10px;
        }
        .col {
          padding-left: 35px;
        }
        .van-col--16 {
          // width: 100px;
          height: 44px;
        }
      }
      #row_name {
        margin-top: 20px !important;
      }
      #row_type {
        margin-top: 25px;
      }
    }
   .btn {
      position: absolute;
      bottom: 0;
      width: 100%;
      height: 49px;
      > button {
        width: 50%;
        height: 100%;
        border-radius: 5px;
        color: #1677ff;
        font-size: 18px;
        float: left;
      }
      .text_btn {
        font-weight: 600;
      }
    }
  }
}
</style>


