import Vue from 'vue'
import Router from 'vue-router'
import messengerYuYue from '@/pages/messengeryuyue'
import qrcode from '@/pages/qrcode'
import yuyueInfo from '@/pages/yuyueinfo'
import yuyueCg from '@/pages/yuyueCG'
import yuyue from '@/pages/yuyue.vue'
import bill from '@/pages/bill'

Vue.use(Router)

export default new Router({
  routes: [{
      path: '/',
      redirect: '/yuyue'
    },
    {
      path: '/yuyue',
      name: '',
      component: yuyue
    },
    {
      path: '/messengeryuyue',
      name: '',
      component: messengerYuYue
    },
    {
      path: '/qrcode',
      name: '',
      component: qrcode
    },
    {
      path: '/yuyueinfo',
      name: '',
      component: yuyueInfo
    },
    {
      path: '/yuyuecg',
      name: '',
      component: yuyueCg
    },
    {
      path: '/bill',
      name: '',
      component: bill
    }
  ]
})



