<template>
  <div class="box">
    <!--  -->
    <div class="top_title"></div>
    <!--  -->
    <div class="content_img">
      <!--  -->
      <div class="content_title">
        <img src="../images/title.png" alt />
      </div>
      <!--  -->
      <div class="Stand_back">
        <img src="../images/Stand _back.png" alt />
      </div>
      <!--  -->
      <div class="red_packets">
        <img src="../images/red_packets.png" alt />
        <!--  -->
        <div class="qrcode_box">
          <div id="qrcode" class="qrcode_img"></div>
        </div>
        <!--  -->
        <div class="save_share_rqcode">
          <img src="../images/save_share_rqcode.png" alt />
        </div>
        <!--  -->
        <div class="save_btn" @click="savrBtn()">
          <img src="../images/save_btn.png" alt="">
        </div>
        <!--  -->
        <div class="share_btn" @click="shareBtn()">
          <img src="../images/share_btn.png" alt="">
        </div>
      </div>

      <!--  -->
      <div class="bottom_box">
        <img src="../images/bottom_box.png" alt="">
      </div>
    </div>
    <van-popup v-model="show" class="share_pop">
        <img src="../images/bill_share.png" alt="">
        
    </van-popup>
    <!--  -->
    <!-- <van-cell @click="shareImg()" />
    <van-share-sheet
      v-model="showShare"
      description=""
      title=""
      :options="options"
      @select="onSelect"
    /> -->
     <!-- <van-popup v-model="show" class="shareBill"> -->
      <!-- <h1></h1> -->
      <!-- <h1 style="color: #666; width: 0px; height: 40px"> </h1> -->
      <!-- <p> </p> -->
    <!-- </van-popup> -->

       <!-- <van-popup v-model="isShow" class="shareBill">
      <h1></h1>
      <p></p>
      <p>  </p>
      <div class="qrcode_bill_box">
        <img src="../images/qrcodebill.png" alt />
      </div>
    </van-popup> -->
  </div>
</template>

<script>
import QRCode from "qrcodejs2";
import wx from "weixin-js-sdk";
export default {
  name: "bill",
  data() {
    return {
      showShare: false,
      show: false,
    };
  },
  methods: {
    // 
    savrBtn() {
      // this.downs()
     this.$toast('')
     
      
    },
    shareBtn() {
      //  this.showShare = true
      // this.$toast(' ')
      this.show = true;
      
    },
     onSelect(index) {
       console.log(index);
       
        if(index.icon ==  "poster") {
          this.show = true
      }
      },
     
    //
    qrcode() {
      // divid id  class
      // 
      let qrCode = new QRCode("qrcode", {
        width: 150,
        height: 150,
        text: "http://crm.xfd365.com/#/yuyue"
      });
    },
     // 
    downs() {
      // //canvas
      let myCanvas = document
        .getElementById("qrcode")
        .getElementsByTagName("canvas");
      let img = document.getElementById("qrcode").getElementsByTagName("img");
      // // //a
      let a = document.createElement("a");
      // //ahrefcanvaspng
      let imgURL = myCanvas[0].toDataURL("image/jpg");
      let ua = navigator.userAgent;
      if (ua.indexOf("Trident") != -1 && ua.indexOf("Windows") != -1) {
        // IE   windows  ;
        var bstr = atob(imgURL.split(",")[1]);
        var n = bstr.length;
        var u8arr = new Uint8Array(n);
        while (n--) {
          u8arr[n] = bstr.charCodeAt(n);
        }
        var blob = new Blob([u8arr]);
        window.navigator.msSaveOrOpenBlob(blob, "" + "." + "png");
      } else if (ua.indexOf("Firefox") > -1) {
        //
        let blob = this.base64ToBlob(imgURL); //new Blob([content]);
        let evt = document.createEvent("HTMLEvents");
        evt.initEvent("click", true, true); //initEvent FF  
        a.download = " "; //
        a.href = URL.createObjectURL(blob);
        a.dispatchEvent(
          new MouseEvent("click", {
            bubbles: true,
            cancelable: true,
            view: window
          })
        ); //
      } else {
        //
        img.src = myCanvas[0].toDataURL("image/jpg");
        // a.href = myCanvas[0].toDataURL('image/png').replace('image/png', 'image/octet-stream')
        a.href = img.src;
        //
        a.download = "";
        //
        a.click();
      }
    },
    //base64blob
    base64ToBlob(code) {
      let parts = code.split(";base64,");
      let contentType = parts[0].split(":")[1];
      let raw = window.atob(parts[1]);
      let rawLength = raw.length;
      let uInt8Array = new Uint8Array(rawLength);
      for (let i = 0; i < rawLength; ++i) {
        uInt8Array[i] = raw.charCodeAt(i);
      }
      return new Blob([uInt8Array], { type: contentType });
    },
  },
  mounted() {
    this.qrcode();
  },
   created() {
    //url
    var localUrl = location.href.split("#")[0];
    //url
    var Url = "url=" + localUrl;
    //
    var nonceStr, signature, timestamp, appId, shareUrl;
    this.$axios
      .post("http://sy.xfd365.com/wx/jssdk2/getSignPackage", { url: localUrl })
      .then(res => {
        //
        var appId = res.data.appId;
        var nonceStr = res.data.nonceStr;
        var signature = res.data.signature;
        var timestamp = res.data.timestamp;
        var shareUrl = res.data.url;
        //config
        wx.config({
          debug: true, // false true
          appId: appId,
          timestamp: timestamp,
          nonceStr: nonceStr,
          signature: signature,
          jsApiList: [
            "checkJsApi",
            "onMenuShareTimeline",
            "onMenuShareAppMessage",
            "onMenuShareQQ",
            "onMenuShareWeibo"
          ]
        });
        //
        window.share_config = {
          share: {
            imgUrl: "http://crm.xfd365.com/images/yuyueicon.png", // 
            desc: "",
            title: "", // 
            link: 'http://crm.xfd365.com/bill', // 
            success: function(rr) {
              //console.log('' + JSON.stringify(tt));
              alert('111111111111')
            },
            cancel: function(tt) {
              //console.log('' + JSON.stringify(tt));
            }
          }
        };
        wx.ready(function() {
          wx.onMenuShareAppMessage(window.share_config.share); // 
          wx.onMenuShareTimeline(window.share_config.share); // 
          wx.onMenuShareQQ(window.share_config.share); // QQ
        });
      });
  },
};
</script>

<style lang="less" scoped>
