      height: 100%;
    }
  }
  .red_packets {
    width: 375px;
    height: 498px;
    position: relative;
    > img {
      width: 100%;
      height: 100%;
    }
    .qrcode_box {
      width: 150px;
      height: 150px;
      display: inline-block;
      position: absolute;
      top: 60px;
      left: 113px;
      .qrcode_img {
        width: 140px;
        height: 140px;
        display: inline-block;
        > img {
          width: 100%;
          height: 100%;
          display: inline-block;
          background-color: #fff; //
          padding: 6px; // padding
        }
      }
      
    }
    .save_share_rqcode {
        width: 200px;
        height: 40px;
        position: absolute;
        top: 334px;
        left: 87px;
        > img {
          width: 100%;
          height: 100%;
        }
    }
 
 

    .save_btn {
      width: 128px;
      height: 46px;
      position: absolute;
      top: 397px;
      left: 52px;
      > img {
        width: 100%;
        height: 100%;
      }
    }

    .share_btn {
      width: 128px;
      height: 46px;
      position: absolute;
      top: 397px;
      left: 194px;
      > img {
