        height: 100%;
      }
    }

  }
  .bottom_box {
      width: 375px;
      height: 58px;
      position: absolute;
      bottom: 0;
      > img {
        width: 100%;
        height: 100%;
      }
    }
  //     .van-popup .van-popup--center {
  //     width: 200px!important;
  //     height: 100px!important;
  //     > img {
  //       width: 100px;
  //       height: 100px;
  //     }
  // }
  .share_pop {
      width: 300px!important;
      height: 600px!important;
      // border-corner-image: url(../images/bill_share.png);
      > img {
        width: 100%;
        height: 100%;
        // margin-left: 40px;
        // margin-top: 70px;
      }
      > p{
        font-size: 20px;
        margin-left: 20px;
        margin-top: 20px;
      }
  }
}
</style>


