<template>
  <div id="clear_flex">
    <!--  -->
    <div class="top_title"></div>
    <!--  -->
    <div class="content">
      <!--  -->
      <div class="content_img"></div>
      <!--  -->
      <div class="content_title">
        <img src="../images/content_title.png" alt />
      </div>
      <!--  -->
      <div class="content_from">
        <img src="../images/content_from.png" alt />
        <van-form class="content_from_id">
          <van-field
            class="id_text"
            v-model="phone"
            type="IDcard"
            name=""
            label=""
            placeholder=""
            trigger="onBlur"
            style="caret-color: #01CBA8;"
            show-error
            clearable
            border
            :rules="[{ validator}]"
          />
          <van-button
            round
            block
            type="info"
            class="idBtn"
            :class="[btnColor==true?'btnColor':'']"
            @click="form_val()"
          ></van-button>
        </van-form>
      </div>
    </div>
  </div>
</template>

<script>

export default {
  name: "yuyue",
  data() {
    return {
      phone: "",
      btnColor: false
    };
  },
  methods: {
    form_val() {
      this.show = true;
    },
    //  true false 
  validator(val){
    // 1518151817X
    var myreg = /^[1][3,4,5,7,8][0-9]{9}$/;
      if (this.phone == "") {
        this.$toast("");
      } else if (!myreg.test(this.phone)) {
        this.$toast("");
    }else {
      //  console.log('1111')
      this.$router.push({
       path: '/messengerYuYue',
       query: {phone: this.phone}
       
      })
    }
    
  },
  },
  watch: {
    phone: function(newVal) {
      if (newVal.length > 0) {
        this.btnColor = true;
      } else {
        this.btnColor = false;
      }
    }
  }
}
</script>

<style lang="less" scoped>
#clear_flex:after {
  content: "";
  clear: left;
  display: block;
}
.top_title {
  width: 375px;
  height: 44px;
  font-size: 18px;
  line-height: 44px;
  text-align: center;
  font-family: ;
  font-weight: 500;
  color: #333;
  background-color: #ffffff;
}
.content {
  width: 375px;
  height: 768px;
  position: relative;
  .content_img {
    position: relative;
    width: 100%;
    height: 100%;
    background-image: url(../images/bg.png);
    background-attachment: fixed;
    background-repeat: no-repeat;
    background-size: cover;
    float: left;
  }
  .content_title {
    position: absolute;
    top: 49px;
    left: 53px;
    width: 261px;
    height: 90px;
    > img {
      width: 100%;
      height: 100%;
    }
  }
  .content_from {
    position: absolute;
    top: 154px;
    left: 19px;
    width: 337px;
    height: 286px;
    > img {
      width: 100%;
      height: 100%;
    }
    .van-cell:not(:last-child)::after {
      border-bottom: 0;
    }
    .content_from_id {
      float: right;
      position: absolute;
      top: 120px;
      width: 320px;
      height: 48px;
      .id_text {
        font-size: 17px;
        margin-left: 12px;
        color: #333;
      }
      .idBtn {
        width: 238px;
        height: 38px;
        margin-top: 42px;
        margin-left: 49px;
        background-color: #d8e9ff;
        border: 1px solid #d8e9ff;
      }
      .btnColor {
        background-color: #006bf6;
        border: 1px solid #006bf6;
      }
    }
  }
}
</style>


