<template>
  <div class="box">
    <!--  -->
    <div class="top_title"></div>
    <!--  -->
    <div class="content_img"></div>
    <!--  -->
    <div class="yuyueInfo">
      <img src="../images/yuyueinfo.png" alt />
      <div class="yuyueinfo_from">
        <van-cell-group class="yuyueinfo_from_name">
          <van-field
            class="field field_name"
            v-model="yu_name"
            label=""
            style="caret-color: #01CBA8;"
            placeholder=""
            clearable
            autosize
          />
          <!--  -->
          <van-field
            class="field"
            style="caret-color: #01CBA8;"
            v-model="tel"
            type="tel"
            label=""
            placeholder=""
            clearable
            autosize
          />
          <van-field
            class="field"
            v-model="sms"
            center
            clearable
            label=""
            style="caret-color: #01CBA8;"
            placeholder=""
            autosize
          >
            <template #button>
              <van-button
                size="small"
                style="border: 0; color:  "
                @click="getAuthCode()"
                v-show="sendAuthCode"
              ></van-button>
              <!-- <span
                v-show="sendAuthCode"
                class="auth_text auth_text_blue"
                @click="getAuthCode"
              ></span> -->
              <span v-show="!sendAuthCode" class="auth_text">
                <span class="auth_text_blue">{{auth_time}}</span> 
              </span>
            </template>
          </van-field>
          <van-button type="info" class="infoBtn" @click="handle()"></van-button>
        </van-cell-group>
      </div>
    </div>
  </div>
</template>


<script>
export default {
  name: "yuyueinfo",
  data() {
    return {
      yu_name: "",
      tel: "",
      sms: "",
      sendAuthCode: true,
      auth_time: ''
    }
  },
  methods: {
   
    // yanzhengma() {
    //   // console.log(this.tel);
    //   this.$api
    //     .get_code({
    //       phone: this.tel,
    //       token:
    //         "eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9.eyJpYXQiOjE1OTA2NDQyOTQsImp0aSI6ImJhZDA2MzdiMDNmZmIxZmViMjg1ZjFlMjhjMjg1ZGE0IiwiaXNzIjoiMTI3LjAuMC4xIiwibmJmIjoxNTkwNjQ0Mjk0LCJleHAiOjE1OTE1MDgyOTQsImRhdGEiOnsidXNlcl9pZCI6MTY2fX0.JYshJF-E3mhbAXJbV13vup87Flt4FpSAyy6jrncAfXA"
    //     })
    //     .then(res => {
    //       console.log(res);
    //       if (res.code == 1) {
    //         this.$toast(res.msg);
    //       } else if (res.code == 0) {
    //         this.$toast(res.msg);
    //       }
    //     });
    // },

    // 
    getAuthCode: function() {
      this.auth_time = 60;
      var myreg = /^[1][3,4,5,7,8][0-9]{9}$/;
      if (this.tel == "") {
        this.$toast("");
      } else if (!myreg.test(this.tel)) {
        this.$toast("");
      } else {
        this.$api
          .get_code({
            phone: this.tel,
            token:
              "eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9.eyJpYXQiOjE1OTA2NDQyOTQsImp0aSI6ImJhZDA2MzdiMDNmZmIxZmViMjg1ZjFlMjhjMjg1ZGE0IiwiaXNzIjoiMTI3LjAuMC4xIiwibmJmIjoxNTkwNjQ0Mjk0LCJleHAiOjE1OTE1MDgyOTQsImRhdGEiOnsidXNlcl9pZCI6MTY2fX0.JYshJF-E3mhbAXJbV13vup87Flt4FpSAyy6jrncAfXA"
          })
          .then(res => {
            if (res.code == 1) {
              this.sendAuthCode = false;
              var auth_timetimer = setInterval(() => {
                this.auth_time--;
                if (this.auth_time <= 0) {
                  this.sendAuthCode = true;
                  clearInterval(auth_timetimer);
                }
              }, 1000);
            } else {
              this.$toast(res.msg);
            }
          });
      }
       var vConsole = new VConsole()
    },
    handle() {
      var name = this.yu_name;
      var phone = this.tel;
      var sms = this.sms;
      var myreg = /^[1][3,4,5,7,8][0-9]{9}$/;
      if (name.trim().length == "") {
        return this.$toast("");
      } else if (!myreg.test(phone)) {
        return this.$toast("");
      } else if (sms.trim().length < 6 || sms.trim().length > 6) {
        return this.$toast("");
      } else {
        this.$router.push("/yuyueCG");
      }
    }
  }
};
</script>


<style lang="less" scoped>
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  border-bottom: 0 !important;
}

.box {
  width: 375px;
  height: 768px;
  position: relative;
  .content_img {
    width: 100%;
    height: 100%;
    background-image: url(../images/bg.png);
    background-attachment: fixed;
    background-repeat: no-repeat;
    background-size: cover;

    float: left;
  }
  .top_title {
    width: 375px;
    height: 44px;
    font-size: 18px;
    line-height: 44px;
    text-align: center;
    font-family: ;
    font-weight: 500;
    color: #333;
    background-color: #ffffff;
  }
.van-cell::after {
  border-bottom: 0;
}
  .yuyueInfo {
    width: 337px;
    height: 374px;
    position: absolute;
    bottom: 252px;
    left: 19px;
    > img {
      width: 100%;
      height: 100%;
    }
    .element.style {
      border-bottom: 0 !important;
    }
    .yuyueinfo_from {
      position: relative;
      .van-hairline--top-bottom::after,
      .van-hairline-unset--top-bottom::after {
        border-width: 0;
      }

      .yuyueinfo_from_name {
        position: absolute;
        top: -300px;
        width: 300px;
        height: 120px;
        margin-left: 10px;
        font-size: 17px;
        color: black;
        text-align: center;
        font-weight: 500;
        // border: 1px solid #fff;
        .field {
          // height: 50px;
          line-height: 20px;
          height: 20px;
          width: 300px;
          margin-top: 30px;
        }
      
        .infoBtn {
          width: 238px;
          height: 37px;
          border-radius: 18px;
          margin-top: 60px;
          margin-left: 10px;
        }
        .auth_text {
          display: inline-block;
          width: 90px;
          // text-align: right;
          // height: 80px;
          margin-right: 20px;
          font-size: 9px;
          .auth_text_blue {
          font-size: 12px;
        }
        }
        
      }
    }
  }
}
</style>


